import { useState, useEffect } from 'react';
import { InspectorControls, useBlockProps } from '@wordpress/block-editor';
import { PanelBody, SelectControl, TextControl } from '@wordpress/components';
import { NEWS_API_KEY } from './config';
const categories = ['technology', 'business', 'sports', 'health', 'science', 'entertainment'];
const countries = ['us', 'gb', 'in', 'au', 'ca'];

export default function Edit({ attributes, setAttributes }) {
  const { category, country, source, articleCount } = attributes;
  const [articles, setArticles] = useState([]);

  useEffect(() => {
    const apiKey = NEWS_API_KEY; // Replace with your NewsAPI key
    let url = `https://newsapi.org/v2/top-headlines?apiKey=${apiKey}&pageSize=${articleCount}`;

    if (category) url += `&category=${category}`;
    if (country) url += `&country=${country}`;
    if (source) url += `&sources=${source}`;

    fetch(url)
      .then(res => res.json())
      .then(data => setArticles(data.articles || []))
      .catch(err => console.error('Error fetching news:', err));
  }, [category, country, source, articleCount]);

  const blockProps = useBlockProps();

  return (
    <>
      <InspectorControls>
        <PanelBody title="News Feed Settings">
          <SelectControl
            label="Category"
            value={category}
            options={categories.map(cat => ({ label: cat, value: cat }))}
            onChange={(val) => setAttributes({ category: val })}
          />
          <SelectControl
            label="Country"
            value={country}
            options={countries.map(c => ({ label: c.toUpperCase(), value: c }))}
            onChange={(val) => setAttributes({ country: val })}
          />
          <TextControl
            label="Source (optional)"
            value={source}
            onChange={(val) => setAttributes({ source: val })}
          />
          <SelectControl
            label="Number of Articles"
            value={articleCount}
            options={[3, 5, 10].map(n => ({ label: `${n}`, value: n }))}
            onChange={(val) => setAttributes({ articleCount: parseInt(val) })}
          />
        </PanelBody>
      </InspectorControls>

      <div {...blockProps} className="news-feed-block">
        {articles.map((article, index) => (
          <div key={index} className="news-card">
            {article.urlToImage && <img src={article.urlToImage} alt={article.title} />}
            <h4>{article.title}</h4>
            <p>{article.description}</p>
            <a href={article.url} target="_blank" rel="noopener noreferrer">Read more</a>
          </div>
        ))}
      </div>
    </>
  );
}
